<template>
    <div>
        <div class= "login-box">
            <img class="avatar" src="@/assets/logo.jpg" alt="Logo de MULTIPAN">
            <h1>Registrate</h1>
            <form @submit.prevent="obtenerKeys()">
                <div class="fullName-container">
                    <div>
                        <!--Nombre Usuario-->
                        <label for="Nombre">Nombre</label>
                        <input id="Nombre" type="text" placeholder="Nombre" v-model="user.name">
                    </div>

                    <div>
                        <!--Apellido Usuario-->
                        <label for="Apellidos">Apellidos</label>
                        <input id="Apellidos" type="text" placeholder="Apellidos" v-model="user.apellidos">
                    </div>
                </div>
                <!--Correo-->
                <label for="Email">Email</label>
                <input id="Email" type="text" placeholder="ejemplo@email.com" v-model="user.email">

                <div class="password-container">
                    <div>
                        <!--Password-->
                        <label for="password">Contraseña</label>
                        <input id="password" type="password" placeholder="Contraseña" v-model="user.password">
                    </div>

                    <div>
                        <!--Confirmar contraseña-->
                        <label for="Confirmación">Confirmación</label>
                        <input id="Confirmación" type="password" placeholder="Confirmación" v-model="confirmarPassword">
                    </div>
                </div>
                <!--Enlaces-->
                <input id="ingresar" type="submit" value="Ingresar">
            </form>
        </div>

    </div>
</template>

<script>
export default {
    name: 'Signup',
    data() {
        return {
            // keys: "",
            user: {email:"", password:"", name:"", apellidos: ""},
            confirmarPassword: ""
        }
    },
    methods: {

        obtenerKeys(){
            // console.log(this.$router.push('/principal'))
            this.axios.get('/keys')
            .then(res=>{
                const keys = res.data;
                const allEmail = []
                for (var i in  keys){
                    //Agregar todos los email a una constante
                    allEmail.push(keys[i].email)
                }

                // Validar si los campos no estan vacios
                if (this.user.email && this.user.password && this.user.name && this.user.apellidos) {
                    // Validar si el email esta en uso 
                    if(allEmail.includes(this.user.email)){
                        console.log('El corre ya está en uso, desea inciar sesion')
                    }else if(this.confirmarPassword !== this.user.password){
                        console.log('Las contraseñas son diferentes')
                    }
                    else{
                        this.newUser()
                    }
                } else {
                    console.log("Por favor ingrese todos los datos")
                }
                
                
            })
            .catch(e=>{

                console.log(e.response)

            })
        },

        newUser(){
            
            this.axios.post('/nueva-key', this.user)
            .then(res=>{
                this.user.push(res.data);
                this.user.email="";
                this.user.password="";
                this.user.name="";
                this.user.apellidos="";
                this.$router.push('/principal')
                
            })
            .catch(e=>{

                console.log(e.response);

            })
        }
    }
}
</script>


<style scoped>
body{
    margin: 0;
    padding: 0;
    background-color: white;
    font-family: yellosun;
}

.login-box{
    width: 520px;
    height: 420px;
    background-color: white;
    color: black;
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    box-sizing: border-box;
    padding: 70px 30px;
}

.login-box .avatar{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: absolute;
    top: -50px;
    left: calc(50% - 50px);
}

.login-box h1{
    margin: 0;
    padding: 0 0;
    text-align: center;
    font-size: 22px;
}

.login-box h2{
    margin: 0;
    padding: 0 0 20px;
    text-align: center;
    font-size: 15px;
    font-family: letters for learners;
}

.login-box label{
    margin: 0;
    padding: 0;
    font-weight: bold;
    display: block;
    font-family: letters for learners
}

.login-box input{
    width: 100%;
    margin-bottom: 20px;
}

.login-box input[type="text"],
.login-box input[type="password"]{
    border: none;
    border-bottom: 1px solid #000;
    background: transparent;
    outline: none;
    color: #000;
    font-size: 16px;
}

.login-box input[type="submit"]{
    border: none;
    outline: none;
    height: 40px;
    background: #C45D1B;
    color: white;
    font-size: 18px;
}

.login-box a{
    margin: 0;
    text-decoration: none;
    font-size: 15px;
    line-height: 20px;
    color: #C45D1B;
    font-family: letters for learners;
}

.login-box a:hover{
    color: #813d12;
}


.fullName-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.password-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}


@media screen and (max-width: 600px){

    .login-box{
        width: 100%;
    }

    .fullName-container {
        flex-direction: column;
    }

    .password-container {
        flex-direction: column;
    }
}
</style>